.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;

  &__wrapper {
    padding: 120px 120px 35px 35px;
    padding-right: calc(95px + 3%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100vh;
  }

  &__text {
    position: relative;
    z-index: 1;
    padding: 3%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 60%;
    height: 100%;
    overflow: hidden;
  }

  &__illustration {
    width: 40%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: bottom;
    background-position-x: center;
    box-shadow: 1px 1px 10px 0px #ffffff75;
  }

  &__number-bcg {
    position: absolute;
    left: 15vh;
    z-index: -1;
    font-size: 30vh;
    font-weight: 800;
    color: #ffffff1e;
  }
}

@media screen and (max-width: 1125px) {
  .item {
    &__text {
      padding-right: 30px;
      width: 60%;
    }
  }
}

@media screen and (max-width: 750px) {
  .item {
    &__wrapper {
      padding-top: 75px;
      align-items: start;
      flex-direction: column;
    }

    &__text {
      padding: 0;
      width: 100%;
    }

    &__illustration {
      width: 75%;
      height: 60%;
      background-position-y: 86%;
    }
  }

  .third {
    &__img {
      background-position-y: 70%;
    }
  }

  .second {
    &__img {
      background-position-y: 45%;
    }
  }
}

@media screen and (max-width: 470px) {
  .item {
    &__number-bcg {
      left: 15vw;
      font-size: 30vw;
      font-weight: 800;
      color: #ffffff1e;
    }
  }
}

@media screen and (max-height: 470px) {
  .third {
    &__img {
      background-position-y: 60%;
    }
  }

  .second {
    &__img {
      background-position-y: 40%;
    }
  }
}

.first {
  &__img {
    background-image: url("../images/01.jpg");
  }
}

.second {
  &__img {
    background-image: url("../images/02.jpg");
  }
}

.third {
  &__img {
    background-image: url("../images/03.jpg");
  }
}