.login {
  &__modal {
    display: inline-block;
    width: auto;
    height: 24px;
    padding-left: 30px;
    background-color: transparent;
    background-image: url("../images/iconAccount.svg");
    background-repeat: no-repeat;
    background-position: left;
    font-family: Gilroy;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $white;
  }

  &__modal::after {
    content: "";
    display: block;
    margin: 0 auto;
    width: 0;
    height: 0;
    outline: 0px solid $white;
    transition: 0.3s;
  }
  &__modal:hover:after {
    width: 100%;
    outline: 0.5px solid $white;
    transition: 0.3s;
  }

  &_active {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 30;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: $bcg;
  }
  &__window {
    position: relative;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 250px;
    min-height: 300px;
    background-color: $white;
    background-image: url("../images/avatar.png");
    background-size: 50px 50px;
    background-repeat: no-repeat;
    background-position: center 10px;
  }

  &__input {
    padding: 5px;
    display: inline-block;
    width: 80%;
    height: 48px;
    border: 1px solid $bcg;
    border-radius: 5px;
  }

  &__btn-comeIn {
    display: inline-block;
    padding: 10px 20px;
    border: 1px solid $bcg;
    border-radius: 5px;
    background-color: $accent;
    font-size: 16px;
    font-weight: 700;
    color: $bcg;
    transition: transform 0.3s;
  }

  &__btn-comeIn:hover {
    transform: scaleY(1.2) translateY(-3px);
    transition: transform 0.3s;
  }

  &__btn-close {
    position: absolute;
    right: 5px;
    top: 5px;
    display: inline-block;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-image: url("../images/close.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px 30px;
    background-color: transparent;
  }

  &__restore-password {
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 0.15em;
    color: $bcg;
    transition: transform 0.2s;
  }

  &__restore-password:hover {
    color: #d42a2a;
    transform: scale(1.01);
    transition: transform 0.2s;
  }
}

@media screen and (max-width: 600px) {
  .login {
    &__modal {
      display: none;
      height: 48px;
      font-size: 22px;
      line-height: 48px;

      &_active {
        display: block;
      }
    }
  }
}