.start {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;

  &__bg-cloud {
    position: absolute;
    left: -5%;
    top: -15px;
    z-index: 0;
    width: 110%;
    height: 80vh;
    background: linear-gradient(330.24deg, #0b1d2600 31.06%, #0B1D26 100%), url("../images/start/BGCloud.jpg") no-repeat;
    background-size: cover;
  }

  &__bg-midMoun {
    position: absolute;
    left: -5%;
    top: 25vh;
    z-index: 0;
    width: 110%;
    height: 75vh;
    background: url("../images/start/mountainsMiddle.png") no-repeat;
    background-size: cover;
  }

  &__bg-frMoun {
    position: absolute;
    left: -5%;
    bottom: -15px;
    z-index: 5;
    width: 110%;
    height: 50vh;
    background: linear-gradient(180deg, rgba(11, 29, 38, 0) 70%, #0B1D26 98%), url("../images/start/frontMountains.png") no-repeat;
    background-size: cover;
    background-position-y: -1px;
  }

  &__wrapper {
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    z-index: 4;
  }

  &__text {
    width: 70%;
  }

  &__title {
    font-family: "Wremena";
    font-weight: 700;
    font-size: calc(24px + (64 + 64 * 0.7) * ((100vw - 320px)/ 1920)); // 28 88px;
    line-height: calc(30px + (58 + 58 * 0.7) * ((100vw - 320px)/ 1920));//38 100px;
    text-transform: capitalize;
    color: #FFFFFF;
  }
}

@media screen and (max-width: 1006px) {
  .start {
    &__text {
      width: 73%;
    }
  }
}

@media screen and (max-width: 660px) {
  .start {
    &__text {
      width: 77%;
    }
  }
}

@media screen and (max-width: 600px) {
  .start {
    &__text {
      padding-bottom: 80px;
      width: 80%;
    }
  }
}

@media screen and (max-width: 395px) {
  .start {
    &__text {
      width: 85%;
    }
  }
}

@media screen and (min-width: 2000px) {
  .start {
    &__text {
      width: 80%;
    }
  }
}

@media screen and (min-width: 2400px) {
  .start {
    &__text {
      width: 104%;
    }
  }
}