.header {
  width: 100%;
  position: fixed;
  top: 65px;
  z-index: 30;

  &__wrapper {
    position: relative;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 24px;
  }

  &__logo {
    display: inline-block;
    width: 110px;
    height: 24px;
  }

  &__logo-svg {
    width: 110px;
    height: 100%;
    fill: $white;
  }

  &__logo-svg:hover {
    fill: $accent;
  }
}

.header-nav {
  &__items {
    display: flex;
  }

  &__item:not(:last-child) {
    padding-right: 40px;
  }

  &__link {
    font-family: Gilroy;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $white;
  }

  &__link::after {
    content: "";
    display: block;
    margin: 0 auto;
    width: 0;
    height: 0;
    outline: 0px solid $white;
    transition: 0.3s;
  }

  &__link:hover:after {
    width: 100%;
    outline: 0.5px solid $white;
    transition: 0.3s;
  }
}



@media screen and (max-height: 670px) {
  .header {
    top: 45px;
  }
}

@media screen and (max-width: 600px) {
  .header {
    top: 25px;
    min-height: 48px;
    &__nav {
      display: none;
      &_active {
        display: block;
      }
    }

    &__logo {
      height: 48px;
    }

    &__open-burger {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      width: 100%;
      height: 100%;
      background-color: $bcg;
      animation: 0.3s forwards burger-visible;
      &_close {
        animation: 0.3s forwards burger-hidden;
      }
    }
  }

  .header-nav {
    &__items{
      flex-direction: column;
    }

    &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 48px;
      text-align: c;
    }

    &__item:not(:last-child) {
      padding-right: 0;
      margin-bottom: 30px;
    }

    &__link {
      font-size: 24px;
      line-height: 48px;
    }
  }
}