.wrapper {
  width: 94%;
  max-width: 1760px;
}

.page {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: $bcg;
}

.hidde {
  display: none;
  overflow: hidden;
}

main {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: $bcg;
}


