.accent {
  position: relative;
  padding-left: 104px;
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: $accent;
}

.accent::before {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  display: block;
  width: 72px;
  height: 2px;
  background-color: $accent;
}

.title {
  font-family: "Wremena";
  font-weight: 700;
  font-size: calc(22px + (42 + 42 * 0.7) * ((100vw - 320px)/ 1920)); //22 64px;
  line-height: calc(28px + (49 + 49 * 0.7) * ((100vw - 320px)/ 1920)); // 28 77px;
  color: $white;
}

.paragraph {
  display: -webkit-box;
  height: 7em;
  font-family: Gilroy;
  font-weight: 700;
  font-size: calc(14px + (4 + 4 * 0.7) * ((100vw - 320px)/ 1920)); // 14 18px;
  line-height: calc(20px + (12 + 12 * 0.7) * ((100vw - 320px)/ 1920)); // 20 32px;
  color: $white;
  text-overflow: ellipsis;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.btn-more {
  display: inline-block;
  padding-right: 40px;
  font-size: calc(14px + (4 + 4 * 0.7) * ((100vw - 320px)/ 1920)); // 14 18px;
  line-height: calc(20px + (12 + 12 * 0.7) * ((100vw - 320px)/ 1920)); // 20 32px;
  color: $accent;
  background-image: url("../images/arrowRight.png");
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 24px 16px;
}

@media screen and (max-width: 1080px) {
  .paragraph {
    height: 5.5em;
    font-size: 16px;
    line-height: 22px;
    -webkit-line-clamp: 4;
  }
}

@media screen and (max-width: 1025px) {
  .accent {
    padding-left: 50px;
    font-size: 16px;
    line-height: 18px;
    &::before {
      top: 8px;
      width: 35px;
    }
  }
}

@media screen and (max-width: 580px) {
  .accent {
    padding-left: 35px;
    font-size: 14px;
    line-height: 16px;
    &::before {
      top: 8px;
      width: 20px;
    }
  }
}

@media screen and (max-height: 525px) {
  .accent {
    padding-left: 35px;
    font-size: 16px;
    line-height: 20px;
    &::before {
      top: 8px;
      width: 20px;
    }
  }

  .paragraph {
    height: 5.5em;
    font-size: 16px;
    line-height: 22px;
    -webkit-line-clamp: 4;
  }

  .title {
    font-size: 26px;
    line-height: 30px;
  }
}

@media screen and (max-height: 400px) {
  .paragraph {
    height: 4em;
    -webkit-line-clamp: 3;
  }
}

@media screen and (max-height: 360px) {
  .paragraph {
    height: 3em;
    -webkit-line-clamp: 2;
  }
}