.site-nav {
  position: fixed;
  right: 3%;
  top: calc(100vh/2 - 200px/2 );
  z-index: 10;

  &__items {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__item {
    display: block;
    padding-right: 30px;
    min-height: 50px;
    border-right: 2px solid #ffffff5b;
  }

  &__item:hover {
    border-radius: 5px;
    background-color: #94949483;
  }

  &__item_active {
    background-color: transparent;
    border-right: 2px solid #fff;
  }

  &__link {
    display: inline-block;
    min-width: 48px;
    min-height: 48px;
    font-family: Gilroy;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 0.1em;
    line-height: 50px;
    text-align: center;
    color: $white;
  }

  &__link:hover {
    text-shadow: 2px 2px 5px gray;
  }

  &__start{
    pointer-events: none;
  }
}

@media screen and (max-width: 600px) {
  .site-nav {
    top: auto;
    bottom: 35px;
  }
}