@keyframes burger-visible {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes burger-hidden {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(100%);
  }
}





@keyframes fromTopToCenter {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes FromCenterToBottom {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(100%);
  }
}

@keyframes fromCenterToTop {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100%);
  }
}

@keyframes fromBottomToCenter {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  1% {
    opacity: 1;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY()
  }
}



.from-center-to-top {
  animation: 0.5s fromCenterToTop;
  animation-timing-function: ease-out;
}
.from-bottom {
  animation: 0.5s fromBottomToCenter;
  animation-timing-function: ease-out;
}
.from-top {
  animation: 0.5s fromTopToCenter;
  animation-timing-function: ease-out;
}
.from-center-to-bottom {
  animation: 0.5s FromCenterToBottom;
  animation-timing-function: ease-out;
}