.follow-us {
  position: absolute;
  left: 3%;
  top: calc(100vh/2 - 100px);
  display: flex;
  flex-direction: column;
  width: 24px;
  z-index: 10;

  &__title {
    font-family: "Gilroy";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: $white;
    writing-mode: vertical-rl;
  }

  &__link {
    margin-top: 25px;
    display: inline-block;
    width: 24px;
    height: 24px;
  }

  &__svg {
    width: 24px;
    height: 24px;
    fill: $white;
  }

  &__svg:hover {
    fill: $accent;
  }
}

@media screen and (max-width: 600px) {
  .follow-us {
    top: auto;
    bottom: 35px;
  }
}