.footer {
  &__wrapper {
    position: relative;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 76%;
    height: 100vh;
    max-width: 1462px;
  }

  &__title {
    font-family: Gilroy;
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
    color: $white;
    padding-right: 30px;
  }

  &__title-items {
    font-family: Gilroy;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    color: $accent;
  }

  &__link {
    display: inline-block;
    font-family: Gilroy;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: $white;
  }

  &__link::after {
    content: "";
    display: block;
    margin: 0 auto;
    width: 0;
    height: 0;
    outline: 0px solid $white;
    transition: 0.3s;
  }

  &__link:hover:after {
    width: 100%;
    outline: 0.5px solid $white;
    transition: 0.3s;
  }

  &__more-information {
    display: flex;
    justify-content: space-between;
    min-width: 50%;
  }

  &__copyright {
    position: absolute;
    left: 0;
    bottom: 10vh;
    font-family: Gilroy;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #ffffff75;
  }
}

@media screen and (max-height: 450px) {
  .footer {
    &__wrapper {
      margin: 0;
      width: 85%;
      padding-left: 3vw;
    }
    &__title {
      font-size: 20px;
      line-height: 24px;
    }
    &__title-items {
      font-size: 20px;
      line-height: 24px;
    }
    &__link {
      font-size: 14px;
      line-height: 18px;
    }

    &__copyright {
      bottom: 3vh;
      left: 3vw;
    }
  }
}

@media screen and (max-width: 1080px) and (min-height: 450px) {
  .footer {
    &__wrapper {
      flex-direction: column;
      justify-content: center;
      margin: 0;
      width: 85%;
      padding-left: 3vw;
    }

    &__more-information {
      justify-content: space-evenly;
      width: 100%;
    }

    &__items:first-child {
      padding-bottom: 15px;
    }

    &__copyright {
      bottom: 3vh;
      left: 3vw;
    }
  }
}

@media screen and (max-width: 545px) and (min-height: 450px) {
  .footer {
    &__wrapper {
      padding-top: 60px;
      align-items: center;
    }

    &__title-items{
      font-size: 22px;
      line-height: 28px;
    }

    &__link {
      font-size: 16px;
      line-height: 24px;
    }

    &__more-information {
      flex-direction: column;
    }

    &__copyright {
      font-size: 16px;
      line-height: 22px;
      padding-right: 40px;
    }
  }
}

@media screen and (max-width: 440px) and (min-height: 450px) {
  .footer {
    &__wrapper {
      padding-top: 75px;
      align-items: center;
      justify-content: start;
    }

    &__title {
      padding: 15px 0;
      font-size: 16px;
      line-height: 20px;
    }

    &__title-items{
      font-size: 18px;
      line-height: 22px;
    }

    &__link {
      font-size: 14px;
      line-height: 18px;
    }
  }
}